import React from 'react'
import { useEffect, useState, useCallback } from 'react'
import { Button, Card, Col, Container, Row } from 'react-bootstrap'
import './home.css'
import img_banner from '../../data/img_banner.png'
import data from '../../data/db.json'
import { Link, useNavigate } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { add } from '../Redux/cartSlice'
import axios from 'axios'
import SkeletonCard from '../Products/SkeletonCard'

export const Home = () => {


  return (
    <div className='home'>
      <Slide />
      <PolicyCard />
      <TopProducts />
    </div>
  );
}
const TopProducts = () => {
  const dispatch = useDispatch()
  const handleAddtoCart = (product) => {
    dispatch(add(product))
  }
  const navigate = useNavigate();
  const hanldeViewProduct = (productID) => {
    navigate('/products/' + productID)
  }
  const [loading, setLoading] = useState(true)
  const [topProducts, setTopProducts] = useState([])

  useEffect(() => {
    axios.get('https://localhost:7164/api/Products/GetProduct', {
      params: {
        PageIndex: 1,
        PageSize: 15
      }
    })
      .then(res => {
        setTopProducts(res.data.products)
        setLoading(false)
      })
      .catch(err => {
        console.log(err)
      });
  }, [])
  console.log(loading)

  return (
    <div className="container homepage__products">
      <h1 className='main__title  '>Sn phm mi</h1>
      <Row xl={4} md={3} xs={2} className='g-4 gird__product' >
        {!loading ?
          topProducts.slice(0, 4).map((product, index) => (
            <Col key={index}>
              <Card className='card__product'>
                <div className='card__img' onClick={() => hanldeViewProduct(product.id)}>
                  <Card.Img src={`data:image/jpeg;base64,${product.im}`} className='img__product cursor-btn' />
                </div>
                <Card.Body>
                  <Card.Title onClick={() => hanldeViewProduct(product.id)} className='cursor-btn'>{product.name}</Card.Title>
                  <Card.Text>
                    <span>{product.price.toLocaleString('it-IT', { style: 'currency', currency: 'VND' })}</span>
                  </Card.Text>

                </Card.Body>
                <Link className='card__link__btn' to={`/products/${product.id}`}>
                  <Button className='card__btn'  > Xem chi tit </Button>
                </Link>
              </Card>
            </Col>
          ))
          : <SkeletonCard count={4} />}
      </Row>
      <h1 className='main__title'>The Latest</h1>
      <Overlay />
      <h1 className='main__title'>Sn phm bn chy</h1>
      <Row xl={4} md={3} xs={2} className='g-4 gird__product' >
        {!loading ?
          topProducts.map((product, index) => (
            <Col key={index}>
              <Card className='card__product'>
                <div className='card__img' onClick={() => hanldeViewProduct(product.id)}>
                  <Card.Img src={`data:image/jpeg;base64,${product.im}`} className='img__product cursor-btn' />
                </div>
                <Card.Body>
                  <Card.Title onClick={() => hanldeViewProduct(product.id)} className='cursor-btn'>{product.name}</Card.Title>
                  <Card.Text>
                    <span>{product.price.toLocaleString('it-IT', { style: 'currency', currency: 'VND' })}</span>
                  </Card.Text>

                </Card.Body>
                <Link className='card__link__btn' to={`/products/${product.id}`}>
                  <Button className='card__btn'  > Xem chi tit </Button>
                </Link>
              </Card>
            </Col>
          ))
          : <SkeletonCard count={12} />}
      </Row>
    </div>
  );
}

const PolicyCard = () => {
  const policy = [
    {
      icon: "fa-solid fa-bag-shopping",
      name: "Min ph giao hng",
      desc: "Min ph vi n hng >150k"
    },
    {
      icon: "fa-solid fa-truck-fast",
      name: "Thanh ton COD",
      desc: "Thanh ton khi nhn hng COD"
    },
    {
      icon: "fa-solid fa-gem",
      name: "Khch hng VIP",
      desc: "u i dnh cho khch hng VIP"
    },
    {
      icon: "fa-solid fa-hand-holding-heart",
      name: "H tr bo hnh",
      desc: "H tr i tr nhanh chng"
    },

  ]
  return (
    <div className="container">

      <Row xl={4} md={2} xs={1} className='g-2 policy-card'>
        {
          policy.map((p, index) => (
            <Col key={index}>
              <Card className="policy-card__item">
                <Row className='policy__desc'>
                  <Col className="policy-card__icon" xl={3} md={3} xs={3}>
                    <i className={p.icon}></i>
                  </Col>
                  <Col className="policy-card__info" xl={9} md={9} xs={9}>
                    <div className="policy-card__info__name">
                      <div className="span">{p.name}</div>
                    </div>
                    <div className="policy-card__info__description">
                      <div className="span">{p.desc}</div>

                    </div>
                  </Col>
                </Row>
              </Card>
            </Col>
          ))
        }
      </Row>

    </div>
  );
}
const Slide = () => {

  return (
    <div className="slide container">
      <Row xs={1} md={2} xl={2} className="align-items-center flex-column-reverse flex-md-row ">
        <Col className="slide__info" xl={8}>
          <div className="slide__info__title">
            <span>Air Jordan 1 Mid SE</span>
          </div>
          <div className="slide__info__desc">
            <span>This AJ1 is all about love. Self-love, love of the game, love for lifewhatever the L-word evokes for you, this foray into floral footwear symbolises the passions that push you forwards. Want to get in the weeds? Just open the box: special-edition packaging names every flower in the bouquet, plus their hidden meanings. Or you can tear past the paper and let the beauty of the blooms gracing the collar and hangtag speak for themselves. Either way, you (and the whole world) are going to fall in love with your look.</span>
          </div>
          <div className="slide__info__btn">
            <span>Xem chi tit</span>
          </div>

        </Col>
        <Col className="slide__img" xl={4}>
          <img className='slide__img' src={img_banner} />
        </Col>
      </Row>
    </div>
  );
}

