import React from 'react'

const Validate = (data, type) => {
  const errors = {};
  const regaxPhoneNumberVietNam = /(84|0[3|5|7|8|9])+([0-9]{8})\b/g;
  //     Secure Password requirements

  // Password must contain at least one digit [0-9].
  // Password must contain at least one lowercase Latin character [a-z].
  // Password must contain at least one uppercase Latin character [A-Z].
  // Password must contain at least one special character like ! @ # & ( ).
  // Password must contain a length of at least 8 characters and a maximum of 20 characters.
  if (type === "signUp") {
    // validate Name
    if (!data.Name.trim()) {
      errors.Name = "Nhp tn ca bn";
    } else {
      delete errors.Name;
    }
    // validate Name
    if (!data.PhoneNumber.trim()) {
      errors.PhoneNumber = "Nhp s in thoi";
    } else if (!regaxPhoneNumberVietNam.test(data.PhoneNumber)) {
      errors.PhoneNumber = "S in thoi khng ng";
    }
    else {
      delete errors.PhoneNumber;
    }
    // validate Adress
    if (!data.Address.trim()) {
      errors.Address = "Nhp a ch";
    } else {
      delete errors.Address;
    }
    // validate Email
    if (!data.Email) {
      errors.Email = "Nhp email ca bn";
    } else if (!/\S+@\S+\.\S+/.test(data.Email)) {
      errors.Email = "Sai nh dng email";
    } else {
      delete errors.Email;
    }
    // validate Password
    if (!data.Password) {
      errors.Password = "Nhp mt khu";
    }else if (data.Password.length < 8) {
      errors.Password = "Mt khu cn t nht 8 k t";
    }
    else if (!data.Password.match(/[A-Z]/)) {
      errors.Password = "Mt khu phi c t nht 1 ch ci vit hoa";
    }
    else if (!data.Password.match(/[0-9]/)) {
      errors.Password = "Mt khu phi c t nht 1 s";
    }
    else if (!data.Password.match(/[!@#$%^&?*]/)) {
      errors.Password = "Mt khu phi c t nht 1 k t c bit";
    } else {
      delete errors.Password;
    }
    // validate confrim password
    if (!data.ConfrimPassword) {
      errors.ConfrimPassword = "Xc nhn mt khu";
    } else if (data.ConfrimPassword !== data.Password) {
      errors.ConfrimPassword = "Mt khu khng khp";
    } else {
      delete errors.ConfrimPassword;
    }
  } else {
    // validate Email
    if (!data.Email) {
      errors.Email = "Nhp email ca bn";
    } else if (!/\S+@\S+\.\S+/.test(data.Email)) {
      errors.Email = "Sai nh dng email";
    } else {
      delete errors.Email;
    }
    // validate Password
    if (!data.Password) {
      errors.Password = "Nhp mt khu";
    } else if (data.Password.length < 8) {
      errors.Password = "Mt khu cn t nht 8 k t";
    } else {
      delete errors.Password;
    }
  }
  return errors;
};
export default Validate